// export default App;

import React, {useState} from 'react';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Tooltip from '@material-ui/core/Tooltip';
import Comp from './Comp';

const App = () => {

    const [inputList, setInputList] = useState("");
    const [Items, setItems]= useState([""]);

    const itemEvent = (event) =>{
        setInputList(event.target.value);

    };

    const listofitems = () =>{
        setItems((oldItems) =>{
            return [...oldItems, inputList];
        });
        setInputList("");



    };
    const deleteItems = (id) =>{
        setItems((oldItems)=>{
            return oldItems.filter((arrElem, index)=>{
                return index !== id;


            })

        })

    };



    return (
        <>
         <div className="main_div flex flex-col bg-indigo-500		items-center	justify-center	 h-screen">
                <div className="sec_div border-2	rounded-xl	h-4/6	  w-96 	bg-slate-100	 shadow-2xl	flex flex-col items-center		shadow-gray-900				 ">

                    <h1 className="  	 	mt-5		flex flex-col items-center	justify-center	"> <p className=" text_list text-center text-5xl	font-medium drop-shadow-xl  		text-indigo-500">ToDo List</p></h1><br />
                    <div className="space-x-3 > * + *	">
                        <input type="text" value={inputList} placeholder="Add Items" onChange={itemEvent} className="items border-indigo-500 border-b-4 text-center text-black outline-none bg-none"/>
                        <Tooltip title="Add">
                        <Button onClick={listofitems}  className="bg-gray-100 	rounded-md  border-2  hover:bg-slate-100 hover:text-indigo-600 hover:border-gray-100	"><AddIcon /></Button>
                    </Tooltip><br/>

                    <ol className="order mt-3 mr-24">
                    <div className="list_items text-xl font-bold capitalize	 text-indigo-500">
                        
                            {/* <li>{Item}</li> */}
                            {Items.map( (itemval, index) => {
                          return  <Comp key={index} id={index} 
                                text={itemval}
                                onSelect={deleteItems}
                            />;

                        })}

                       
                    </div>
                    </ol>


                    </div>
                    

                    
                </div>
            </div>

         


        </>
    )

};

export default App;